<template>
  <div class="row out">
    <div class="col-md-3">
      <div class="upload bg-primary">上传文件</div>
      <input ref="file" type="file" class="change" @change="uploadFile($event.target.files[0])" />
    </div>
    <div class="col-md-9">
      <div class="alert alert-warning" v-show="isf">不支持上传{{fileType}}类型文件</div>
      <div class="alert alert-success" v-show="ish">上传格式合法</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "upLoad",
  props: {
    header: Object,
    action:String
  },
  data() {
    return {
      isf: false,
      ish: false,
      fileType: ""
    };
  },
  methods: {
    uploadFile(v) {
      console.log(v);
      const type = v.type;
      let arr = v.name.split(".");
      let typeName = arr[arr.length - 1];
      console.log(typeName);
      if (typeName === "rar") {
        this.isf = true;
        this.ish = false;
        this.fileType = typeName;
      } else {
        if (type.startsWith("image") || type.endsWith("pdf")) {
          this.ish = true;
          this.isf = false;
        } else if (type.endsWith("javascript")) {
          this.isf = true;
          this.ish = false;
          this.fileType = "javascript";
        } else if (type.endsWith("x-zip-compressed")) {
          this.isf = true;
          this.ish = false;
          this.fileType = "zip";
        }
      }
    }
  },
  created(){
    if(!this.header.token){
      console.log('权限不足');
    }
  }
};
</script>
<style scoped>
.out {
  position: relative;
}
.upload {
  width: 200px;
  height: 30px;
  line-height: 30px;
  color: #fff;
  border-radius: 5px;
  text-align: center;
  cursor: pointer;
  z-index: -10;
}
.change {
  width: 200px;
  height: 30px;
  position: absolute;
  overflow: hidden;
  left: 0;
  top: 0;
  opacity: 0;
  cursor: pointer;
}
</style>
